/**
 * ViewChange
 */

class ViewChange {

  constructor() {
    this.body = document.querySelector('body')
    this.toggler = document.querySelector('#toggleViewMode')
    this.demoViewName = '-demoView'
    this.demoView = false
  }

  init() {
    if (!this.toggler) return
    this.attachEvent()
  }

  attachEvent() {
    this.toggler.addEventListener('click', e => {
      this.demoView = !this.demoView
      this.toggleState()
    })
  }

  toggleState() {
    // „ÇØ„É©„Çπ„ÅÆ‰ªò„ÅëÂ§ñ„Åó
    if (this.demoView) {
      this.body.classList.add(this.demoViewName)
    } else {
      this.body.classList.remove(this.demoViewName)
    }

    // aria„ÅÆÂàá„ÇäÊõø„Åà
    if (this.demoView) {
      this.toggler.textContent = '„Éá„É¢„É¢„Éº„Éâ„Çí„ÇÑ„ÇÅ„Çã'
    } else {
      this.toggler.textContent = '„Éá„É¢„É¢„Éº„Éâ„Å´„Åô„Çã'
    }
  }
}

export default ViewChange
