/**
 * Demo6
 */

class Demo6 {

  constructor() {
    this.toggler = document.querySelector('#hamburger')
    this.opnendName = '-opened'
    this.opened = false
  }

  init() {
    if (!this.toggler) return
    this.attachEvent()
  }

  attachEvent() {
    this.toggler.addEventListener('click', e => {
      this.opened = !this.opened
      this.toggleState()
    })
  }

  toggleState() {
    // „ÇØ„É©„Çπ„ÅÆ‰ªò„ÅëÂ§ñ„Åó
    if (this.opened) {
      this.toggler.classList.add(this.opnendName)
    } else {
      this.toggler.classList.remove(this.opnendName)
    }

    // aria„ÅÆÂàá„ÇäÊõø„Åà
    if (this.opened) {
      this.toggler.setAttribute('aria-label', '„É°„Éã„É•„Éº„ÇíÈñâ„Åò„Çã')
    } else {
      this.toggler.setAttribute('aria-label', '„É°„Éã„É•„Éº„ÇíÈñã„Åè')
    }
  }
}

export default Demo6
